/* 以下開始 */
.header {
  display: flex;
  justify-content: space-between; }

.header img {
  width: 80px;
  height: auto;
  vertical-align: center;
  margin: 30px 0 30px 80px; }

.header ul {
  display: flex;
  align-items: center; }

.header ul li {
  padding: 0 32px; }

.header ul li a {
  display: block; }

@media (max-width: 768px) {
  .header img {
    margin: 30px 0 30px 40px; }
  .header ul li {
    padding: 0 20px; } }

@media (max-width: 375px) {
  .header {
    display: block;
    text-align: center; }
  .header img {
    display: inline-block;
    width: 48px;
    margin: 10px 0; }
  .header h1 a {
    display: block; }
  .header ul {
    flex-wrap: wrap; }
  .header ul li {
    border: #FFFFFF solid 1px;
    width: 50%;
    margin-right: 0px; } }

.container {
  max-width: 1140px;
